
#root {
    position: relative;
    width: 100vw;
    height: 100vh;
}
    
.photobooth_container {
    /* background-color: rgba(0,0,0); */
    position: relative;
    width: 100%;
    min-height: 100vh;
    transform: rotateY(180deg);
}


.frame {
    position: absolute;
    justify-content: center;
    justify-items: center;
    margin: auto;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: 10;
    border-style: solid;
    border-width: 5px;
    transform: rotateY(180deg);
    height: 450px; 
    width: 600px;
}
.countdown {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
    background-color: rgba(0,0,0,0.5);
    font-size: 72px;
    z-index: 20;
    transform: rotateY(180deg);
}

.button_div{ 
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    bottom: 12%;
}
    
.button_div button {
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgb(145, 145, 145);
    bottom: 12%;
    z-index: 10;
}
    
.webcom-container{
    position: absolute;
    margin: auto;
    inset: 0px;
    z-index: -10;
    height: 425px;
}
    
    
    
    
    
    
    